body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, p {
  margin: 0;
}

h1 {
  font-size: 3rem;
}

p {
  font-size: 1.5rem; 
}

#temp-details {
  padding-bottom: 1rem;
}

#temperature {
  font-size: 6rem; 
}

#location header {
  display: flex;
  justify-content: center;
  align-items: center; 
}

#location {
  display: flex;
  flex-direction: row; 
}

@media (max-width: 600px) { 
  #location {
    flex-direction: column; 
  }
}

#logo {
  font-size: 3rem;
  margin-left: 10px
}

#logo-text {
  font-size: 2rem;
  font-style: italic;
}

#form {
  margin: 0 auto;
  margin-top: 20px;
  width: 50%;
}

@media (max-width: 600px) {
  #form {
    width: 90%;
  }
}




#form-input {
  display: inline;
  width: 80%; 
}

#search-button {
  font-size: 2.5rem;
}

#weather-card {
  height: auto;
  margin: 0 auto;
  text-align: center;
}




#details {
  font-size: 2rem;
}

#temperature-button {
  font-size: 2rem
}

.row {
  margin: 0;
}